/* ProjectBar.css */

/* Apply styles to the link itself */
.project-buttons-container a {
    text-decoration: none; /* Remove default underline from links */
}

/* Existing styles */
.project-bar {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #000000;
    padding: 2vw;
    color: rgb(255, 255, 255);
    box-sizing: border-box;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0px -4px 5px rgba(0, 0, 0, 1),
                0px -12px 15px rgba(0, 0, 0, 0.5),
                0px -24px 20px rgba(0, 0, 0, 0);
}

.project-bar-content {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 2vw;
    width: 100%;
}

.similar-projects-title {
    font-size: 2vw;
    margin: 0;
    line-height: 1;
    display: flex;
    align-items: center;
}

.project-buttons-container {
    display: flex;
    gap: 1.5vw;
    align-items: center;
    justify-content: center;
}

.project-button {
    padding: 0.8vw 1.2vw;
    background-color: #f4f4f4;
    color: rgb(0, 0, 0);
    border: none;
    cursor: pointer;
    flex-shrink: 0;
    font-size: 1.8vw;
    font-weight: bold;
    white-space: nowrap;
    display: flex;
    align-items: center;
    border-radius: 1vw;
    transition: background-color 0.3s, color 0.3s;
}

/* Hover styles for button */
.project-button:hover {
    background-color: #ff7300;
    color: #ffffff;
}

/* Ensure link styles do not override button styles */
.project-button:link,
.project-button:visited {
    color: inherit;
    text-decoration: none;
}

.project-button:hover,
.project-button:active {
    color: #ffffff;
}
